<template>
  <div class="foundDetail">
    <div class="mescroll">
      <div class="banner">
        <van-swipe @change="onChange" :show-indicators="false">
          <van-swipe-item v-for="(item,index) in foundDetail.pics" :key="index">
            <img :src="$utils.dealImg(item)" alt />
          </van-swipe-item>
        </van-swipe>
        <div class="custom-indicator">{{ current + 1 }}/{{foundDetail.pics?foundDetail.pics.length:''}}</div>
      </div>
      <div class="info">
        <div class="title">
          <p>{{foundDetail.shopName}}</p>
        </div>
        <div class="labels">
          <div class="label" v-for="(item,index) in foundDetail.shopTitles" :key="index" v-if="item">{{item}}</div>
          <div class="label color">{{foundDetail.shopLevelName}}</div>
        </div>
        <div class="flex">
          <div class="list">
            <!-- <div class="item">
              <p class="left">电话：</p>
              <p class="right">{{foundDetail.shopMobile}}</p>
            </div> -->
            <div class="item">
              <p class="left">地址：</p>
              <p class="right">{{(foundDetail.area?foundDetail.area.treeNames?foundDetail.area.treeNames.replace(/\//g,''):'':'') + foundDetail.shopAddr}}</p>
            </div>
            <div class="item">
              <p class="left">营业时间：</p>
              <p class="right">{{foundDetail.shopYingye}}</p>
            </div>
          </div>
          <!-- <img v-if="foundDetail.shopisWeb == 1" class="icon" @click="toMerchant()" src="../../assets/img/merchantsIcon/right2.png" alt=""> -->
        </div>
      </div>
      <div v-html="foundDetail.shopMsg" class="content">{{foundDetail.gmsg }}</div>
      <div class="contact">
         <div class="btn" v-if="foundDetail.shopisWeb == 1" style="margin-bottom:10px;"  @click="toMerchant()">
          <img src="../../assets/img/merchantsIcon/join.png" alt="">
        </div>
        <div class="btn" @click="contact(foundDetail.shopMobile)">
          <img src="../../assets/img/merchantsIcon/telno1.png" alt="">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Swipe, SwipeItem } from "vant";
import { getFoundDetail } from "@/api";
export default {
  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem
  },
  data() {
    return {
      current: 0,
      foundDetail: {}
    };
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    toMerchant() {
      this.$router.push({
        name: "merchantsIndex",
        query: {
          shopCode: this.foundDetail.shopCode
        }
      });
    },
    contact(telno) {
      window.location.href = `tel://${this.foundDetail.shopMobile}`;
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getFoundDetail({
        shopCode: this.$route.query.id
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.foundDetail = res.data;
          this.foundDetail.pics = this.foundDetail.shopHeader.split(",");
          this.foundDetail.shopTitles = this.foundDetail.shopTitle.split(",");
          console.log(this.foundDetail.shopTitles);
          // this.foundDetail.shopMsg = this.foundDetail.shopMsg.replace(
          //   /(<img[\s\S]+?)src=(['"])/g,
          //   '<img src="' + this.$config.http
          // );
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    document.title = "商户详情";
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.foundDetail {
  height: 100vh;
  .banner {
    position: relative;
    .van-swipe-item {
      img {
        width: 100vw;
        // height: calc(100vw / 1.316);
      }
    }
  }
  .info {
    padding: 0 10px;
    border-bottom: 1pt solid #efefef;
    .title {
      display: flex;
      align-items: center;
      p {
        font-size: 15px;
        color: #333;
        flex: 2;
        font-weight: bold;
        line-height: 25px;
      }
      img {
        width: 20px;
      }
    }

    .labels {
      margin: 10px 0 0;
      .label {
        display: inline-block;
        font-size: 13px;
        margin-right: 10px;
        background: #ffa500;
        padding: 3px 8px;
        border-radius: 10px;
        color: #fff;
      }
      .color {
        background: #ffeff2;
        color: #ef5852;
      }
    }
    .flex {
      display: flex;
      align-items: center;
      .list {
        flex: 2;
        padding: 10px 0;
        .item {
          display: flex;
          // align-items: center;
          font-size: 13px;
          line-height: 25px;
          .left {
            width: 80px;
          }
          .right {
            flex: 2;
          }
        }
      }
      .icon {
        width: 15px;
      }
    }
  }
  .contact {
    text-align: right;
    position: fixed;
    right: 15px;
    top: 180px;
    background: #fff;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    .btn {
      img {
        width: 30px;
        height: 30px;
      }
    }
  }
}
</style>
<style lang="scss">
.foundDetail {
  .custom-indicator {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 2px 10px;
    border-radius: 10px;
    color: #fff;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.1);
  }
  .content {
    margin: 20px 0 0;
    img {
      width: 100% !important; 
      display: block;
    }
  }
}
</style>


